import React from "react";
import PropTypes from "prop-types";

import NavItem from "./NavItem/NavItem";
import { NavLink } from "react-router-dom";

import "./navigation.scss";

const Navigation = (props) => {
  return (
    <nav className={`nav ${props.open ? "is-open" : "is-hidden"}`}>
      <ul className="nav__list">
        <NavItem link="/my-wedding-website" closeNav={props.func}>Home</NavItem>
        <div className="nav__top-level1">
          <p className="nav__item">Our Story</p>
          <ul className="nav__sub-list">
            <NavItem link="/my-wedding-website/our-story" closeNav={props.func}>Our Story</NavItem>
            <NavItem link="/my-wedding-website/photos" closeNav={props.func}>Photos</NavItem>
          </ul>
        </div>
        <NavItem link="/my-wedding-website/venue" closeNav={props.func}>Venue</NavItem>
        <NavItem link="/my-wedding-website/travel" closeNav={props.func}>Travel</NavItem>
        <NavItem link="/my-wedding-website/things-to-do" closeNav={props.func}>Things to Do</NavItem>
        <NavItem link="/my-wedding-website/faq" closeNav={props.func}>FAQ</NavItem>
        <NavItem link="/my-wedding-website/rsvp" closeNav={props.func}>RSVP</NavItem>
      </ul>
    </nav>
  );
};

Navigation.propTypes = {
  open: PropTypes.bool.isRequired,
  func: PropTypes.func.isRequired
};

export default Navigation;
